@keyframes p-keyframes-bounce {
  from, 65%, 85% {
    transform: scale(1);
  }

  75% {
    transform: scale(.85);
  }

  82.5% {
    transform: scale(1.05);
  }
}

@keyframes p-keyframes-fade-in {
  to {
    opacity: 1;
  }
}

@keyframes p-keyframes-pulse {
  from, 75% {
    opacity: 1;
    transform: scale(.85);
  }

  to {
    opacity: 0;
    transform: scale(2.5);
  }
}

@keyframes p-keyframes-spin {
  to {
    transform: rotate(1turn);
  }
}

.Polaris-Avatar {
  --pc-avatar-extra-small-size: 1.5rem;
